.btn {
  --bs-btn-padding-x: 0.875rem;
  --bs-btn-padding-y: 0.375rem;
  --bs-btn-font-weight: 500;
  --bs-btn-border-radius: 0.625rem;
  --bs-btn-border-width: 0.125rem;
  --bs-btn-line-height: 1.5;
  --bs-btn-focus-box-shadow: 0 0 0 0.25rem #0082fa;
  box-shadow: none;
}
.btn:focus-visible {
  color: var(--bs-btn-color);
  background-color: var(--bs-btn-bg);
  border-color: var(--bs-btn-border-color);
}
.btn:focus-visible:hover {
  color: var(--bs-btn-hover-color);
  background-color: var(--bs-btn-hover-bg);
  border-color: var(--bs-btn-hover-border-color);
}
.btn > i + *,
.btn > .icon + * {
  margin-left: 0.5rem;
}
.btn > * + i,
.btn > * + .icon {
  margin-left: 0.5rem;
}

.btn-primary {
  --bs-btn-color: #fff;
  --bs-btn-bg: #003b5c;
  --bs-btn-border-color: #003b5c;
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: #33627d;
  --bs-btn-hover-border-color: #33627d;
  --bs-btn-focus-shadow-rgb: 38, 88, 116;
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: #002337;
  --bs-btn-active-border-color: #002337;
  --bs-btn-active-shadow: none;
  --bs-btn-disabled-color: #fff;
  --bs-btn-disabled-bg: #7e7e7e;
  --bs-btn-disabled-border-color: #7e7e7e;
}

.btn-secondary {
  --bs-btn-color: #000;
  --bs-btn-bg: #00a9e0;
  --bs-btn-border-color: #00a9e0;
  --bs-btn-hover-color: #000;
  --bs-btn-hover-bg: #26b6e5;
  --bs-btn-hover-border-color: #26b6e5;
  --bs-btn-focus-shadow-rgb: 0, 144, 190;
  --bs-btn-active-color: #000;
  --bs-btn-active-bg: #33bae6;
  --bs-btn-active-border-color: #1ab2e3;
  --bs-btn-active-shadow: none;
  --bs-btn-disabled-color: #fff;
  --bs-btn-disabled-bg: #7e7e7e;
  --bs-btn-disabled-border-color: #7e7e7e;
}

.btn-success {
  --bs-btn-color: #003b5c;
